

.latest-news {
    position: relative;
    width: 100%;
    max-width: 1140px; 
    margin: 0 auto 0 auto;
    &__sectionText{
        margin-bottom: 32px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 4px;
        &-title{
            line-height: 120%;
            letter-spacing: 2.4px;
            text-transform: uppercase;
        }
    }
    &__inner {
        overflow: hidden;
        width: 100%;
        margin: 0 auto;
    }
    
    &__wrapper {
        display: flex;
        transition: transform 0.5s ease-in-out;
        gap: 20px;
    }
    &__arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 52px;
    height: 52px;
    border-radius: 100%;
    background-color: $primary;
    border: none;
    cursor: pointer;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
    }
    
    &__arrow--left {
    left: -17px; /* Напівширина кнопки для розміщення поза межами слайдера */
    }
    
    &__arrow--right {
    right: -17px; /* Напівширина кнопки для розміщення поза межами слайдера */
    }
    
    &__arrow img {
    width: 12px;
    height: 21px;
    }
    
    &__arrow--left img {
    transform: rotate(180deg);
    }
    
    &__arrow:hover {
    background: $primary_light;
    }
    
    &__indicators {
    bottom: -24px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    z-index: 10;
    }
    
    &__indicators .indicator {
    width: 8px;
    height: 8px;
    background: $lightGray;
    border-radius: 50%;
    margin:0 5px;
    cursor: pointer;
    }
    
    &__indicators .indicator.active {
    background: $darkColor;
    }


    
   
}
.news-item{
    overflow: hidden;
    display: flex;
    height: 476px;
    flex-direction: column;
    gap: 8px;
    box-sizing: border-box;
    min-width: calc(50% - 12px);
    &__title{
        color: $darkColor;
        line-height: 22px;
    }
    &__description{
        line-height: 30px;
        color: $lightGrayText;
    }
    &__image{
        border-radius: 4px;
        background-image: url(../img/test.jpg);
        background-size: cover;
        height: 400px;
    }
}  
@media (max-width: 1174px) {
    .latest-news__inner {
        max-width: 500px ;
    }
    .news-item {
        min-width: 500px;
        max-width: 500px;
    }
    .latest-news {
        max-width: 534px;
    }
    
}

@media (max-width: 640px) {
    .latest-news__arrow {
        width: 36px;
        height: 36px;
    }
    .latest-news{
        &__indicators {
            bottom: 0px;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            display: flex;
            z-index: 10;
        }
        &__arrow--left {
            left: 7px; /* Напівширина кнопки для розміщення поза межами слайдера */
            }
            
            &__arrow--right {
            right: 7px; /* Напівширина кнопки для розміщення поза межами слайдера */
            }
            
            &__arrow img {
            width: 12px;
            height: 20px;
        }
    }
    .latest-news__inner {
        max-width: 320px ;
        height: 340px;
    }
    .news-item {
       
        min-width: 320px;
        max-width: 320px;
        &__image{

            height: 255px;
        }
    }
    .latest-news {
        max-width: 320px;
    }
}
