/* styles.css */

.top-sales {
    position: relative;
    width: 100%;
    max-width: 1140px;
    margin: 0px auto 110px auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;

    &__see-all {
        width: 100%;
        text-align: end;
        padding: 8px;
        font-size: 14px;
        font-weight: 500;
        line-height: 30px;
        margin-bottom: 8px;
        color: $darkColor;
        display: none;
    }

    &__sectionTitle {

        line-height: 120%;
        letter-spacing: 2.4px;
        text-transform: uppercase;
        margin-bottom: 32px;
    }

    &__inner {
        overflow: hidden;
        width: 100%;
        margin: 0 auto;
    }

    &__wrapper {
        display: flex;
        transition: transform 0.5s ease-in-out;
        gap: 24px;
    }

    &__item {
        overflow: hidden;
        display: flex;
        height: 681px;
        flex-direction: column;
        gap: 8px;
        box-sizing: border-box;
        min-width: calc(33.3333% - 16px);
    }

    &__title {
        color: $darkColor;
        line-height: 30px;
        text-transform: capitalize;
    }

    &__price {
        font-weight: 600;
        line-height: 30px;
        text-transform: uppercase;
        color: $darkColor;
    }

    &__image {
        background-size: cover;

        height: 605px;
    }
    &__back-1{
        background-image: url(../img/girl_wear_glasses.jpg);
    }

    &__arrow {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 52px;
        height: 52px;
        border-radius: 100%;
        background-color: $primary;
        /* Замініть на фактичний колір */
        border: none;
        cursor: pointer;
        z-index: 10;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__arrow--left {
        left: -17px;
        /* Напівширина кнопки для розміщення поза межами слайдера */
    }

    &__arrow--right {
        right: -17px;
        /* Напівширина кнопки для розміщення поза межами слайдера */
    }

    &__arrow img {
        width: 12px;
        height: 21px;
    }

    &__arrow--left img {
        transform: rotate(180deg);
    }

    &__arrow:hover {
        background: $primary_light;
    }

    &__indicators {
        bottom: -24px;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        z-index: 10;
    }

    &__indicators .indicator {
        width: 8px;
        height: 8px;
        background: $lightGray;
        border-radius: 50%;
        margin: 0 5px;
        cursor: pointer;
    }

    &__indicators .indicator.active {
        background: $darkColor;
    }




}

@media (max-width: 1174px) {
    .top-sales__inner {
        max-width: 364px;
    }

    .top-sales__item {
        min-width: 364px;
        max-width: 364px;
    }

    .top-sales {
        max-width: 398px;
    }

}

@media (max-width: 640px) {
    .top-sales__arrow {
        width: 36px;
        height: 36px;

        img {
            width: 20px;
            height: 20px;
        }
    }

    .top-sales {
        margin: 0px auto 64px auto;
        &__arrow--left {
            left: 0px;
            /* Напівширина кнопки для розміщення поза межами слайдера */
        }

        &__arrow--right {
            right: 0px;
            /* Напівширина кнопки для розміщення поза межами слайдера */
        }
    }
}

@media (max-width:520px) {
    .top-sales {
        &__see-all {
            width: 100%;
            text-align: end;
            display: block;
        }
        &__indicators {
            bottom: 0px;
        }
        &__item {
            height: 681px;
        }

        &__arrow--left {
            left: 7px;
        }

        &__arrow--right {
            right: 7px;
        }

        &__image {
            height: 258px;
            overflow: hidden;
            border-radius: 4px;
        }
        &__back-1{
            background-image: url(../img/girl_wear_glasses_mob.jpg);
        }

        &__arrow {
            top: 51%;
        }
    }

    .top-sales__inner {
        max-width: 320px;
    }

    .top-sales__item {
        min-width: 320px;
        max-width: 320px;
        height: 358px;
    }

    .top-sales {
        max-width: 320px;
    }
}